.header {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding-top: 20px;
  padding-bottom: 12px;
  background-color: var(--bg-color);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 100;
  &__content {
    display: flex;
    justify-content: space-between;
  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__right {
    display: flex;
    align-items: center;
  }

  &__logo {
    border-radius: 6px;
    border: 1px solid var(--blue);
    width: 28px;
    height: 28px;
    padding: 1px;
    margin-right: 8px;
  }

  &__img {
    border-radius: 3.34px;
  }
}
